@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300&family=Nova+Square&family=Vina+Sans&display=swap');

body, html {
  width: 100%;
  height: 100%;
}
.liens  {
  display: flex;
  flex-direction: row;

}
.liens .lien {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 23px;
  margin-left: 10px;
  width: 15vh !important;
  height: 5vh !important;
  color: cyan;
  box-shadow: none;
  text-decoration: underline;
  font-family: 'Merriweather', serif;
}
.lien.active , .lien:hover {
  color: fuchsia;
  box-shadow: 0 0 5px fuchsia;
  border-radius: 5px;
}

.bg-image{
  background-image: url('https://mdbcdn.b-cdn.net/img/new/fluid/nature/015.webp');
    height: 100vh;
}

@media screen and (max-width:478px) {
  .liens  {
    display: flex;
    flex-direction: column;
  
  }
}
/* counter card styling*/
.counter-card {
  margin-top: 5vh;
}
.counter-card span{
  width: 30vh;
  height: 10vh;
  border-radius: 50px;
background: #e0e0e0;
box-shadow: inset 20px 20px 60px #bebebe,
            inset -20px -20px 60px #ffffff;
            text-align: center;
            font-size: 30px;
            color: darkmagenta;
}
.counter-card button{
width: 20vh;
margin-bottom: 7px;
}

.api {
  max-height: 70vh;
  overflow: scroll;
}